




















export default {
	data() {
		return {
			username: '',
			password: ''
		};
	},
	methods:{
		
		Register(){
			uni.navigateTo({
				url:'../register/register',
			})
		},
		
		login(){
			var username = this.username;
			var password = this.password;
			if (username.length < 1) {
			    uni.showToast({
			        icon: 'none',
			        title: 'Your name is not valid'
			    });
			    return;
			}
			if (password.length < 1) {
			    uni.showToast({
			        icon: 'none',
			        title: 'Your password is not valid'
			    });
			    return;
			}
			
			var ServerUrl = this.$serverUrl;
			
			uni.request({
				
				url:ServerUrl + '/login',
				method:'POST',
				data:{
					username:username,
					password:password,
				},
				success: (res) => {
					console.log(res.data);
					var status = res.data.status;
					if(status == 200){
						uni.showToast({
								icon: 'none',
								title: 'Welcome'
							});
							uni.navigateTo({
								url: '../classify/classify',
							});
					}else if(status == 500){
						uni.showToast({
								icon: 'none',
								title: res.data.msg,
								duration: 3000
							});
					}
					
				}
			})
		},
	},
	
	onLoad: function() {
		uni.setNavigationBarTitle({
			title: "Sign in"
		});
	
	}
	
}
